<template>
	<view style="padding-bottom: 20rpx;">
		<u-navbar title-color="#fff" back-icon-color="#ffffff" :is-fixed="true" :is-back="false"
			:background="{ background: '#1CBBB4' }" :back-text-style="{color: '#fff'}" :title="null">
			<view class="slot-wrap">
				<view class="map-wrap" v-if="true">
					<u-icon name="map" color="#ffffff" size="24"></u-icon>
					<text class="map-wrap-text">贵州师范大学</text>
					<u-icon name="arrow-down-fill" color="#ffffff" size="22"></u-icon>
				</view>
			</view>
		</u-navbar>
		<view class="search">
			<u-search placeholder="输入你想要的内容" style="width: 90%;display: flex;justify-content: center;margin: 0 auto;"
				:show-action="false" v-model="keyword"></u-search>
		</view>
		<view>
			<u-notice-bar color='#D99D1F' bg-color='#F3EBD9' mode="horizontal" :list="list"></u-notice-bar>
		</view>
		<view>
			<u-swiper :list="list1" border-radius='0rpx' height='350'></u-swiper>
		</view>

		<view class="nav">
			<view class="navitem">
				<view class="top">
					<u-image width="100%" mode='aspectFit' height="100%" src="/static/nav/biaobai.png"></u-image>
				</view>
				<view class="title">表白墙</view>
			</view>
			<view class="navitem">
				<view class="top" style="background-color: #AF58F2;">
					<u-image width="100%" mode='aspectFit' height="100%" src="/static/nav/ershou.png"></u-image>
				</view>
				<view class="title">二手闲置</view>
			</view>
			<view class="navitem">
				<view class="top" style="background-color: #F270D0;">
					<u-image width="100%" mode='aspectFit' height="100%" src="/static/nav/kuaidi.png"></u-image>
				</view>
				<view class="title">快递代取</view>
			</view>
			<view class="navitem">
				<view class="top" style="background-color: #4FDC46;">
					<u-image width="100%" mode='aspectFit' height="100%" src="/static/nav/sw.png"></u-image>
				</view>
				<view class="title">失物招领</view>
			</view>
		</view>
		<view style="background-color: #f3f4f6;width: 100%;height: 15rpx;margin-top: 30rpx;">
		</view>
		<view style="margin-bottom: 30rpx;" class="xd">
			<!-- <u-sticky offset-top='231'> -->

			<!-- <view class="sticky"> -->
			<u-tabs :list="listtab" :is-scroll="true  " :current="current" @change="change"></u-tabs>
			<!-- </view> -->
			<!-- </u-sticky> -->


		</view>
		<!-- 表白墙 -->
		<view v-for="item in databbai">
			<view class="textitem">
				<view class="content">
					<view>
						<u-avatar  size='large'
							:src="item.user_id[0].avatar_file ? item.user_id[0].avatar_file.url : ''"></u-avatar>
					</view>
					<view class="name">
						<view style='display: flex;justify-content: space-between;width: 550rpx;'>
							<view style="">
								{{item.user_id[0].nickname}}
							</view>
							<u-tag text="表白墙" :bg-color='color[2]' size='mini' mode="dark" />
							
						</view>
						
						<view style="font-size: 28rpx;color: #C6C8CB;">
							{{$u.timeFormat(item.time,'yyyy-mm-dd hh:MM:ss')}}
						</view>

					</view>

				</view>
				<view class="text">
					<view style="color: #606266;">
						<u-parse :html="item.text"></u-parse>
					</view>
					<!-- 适配图片排列问题 -->
					<view style="margin-top: 20rpx;">
						<image   
								:lazy-load='true' 
								class='u-image' 
								@tap='look(item.img_url[0].url)' 
								:style="{'width':item.img_url[0].image.width*9/35+'rpx'}" 
								mode='widthFix' 
								:src="item.img_url[0].url "
								v-if="item.img_url.length==1 && item.img_url[0].image.width>900"
							>
						</image>
						<image 
							:lazy-load='true' class='u-image'  
							@tap='look(item.img_url[0].url)'
							:style="{'width':item.img_url[0].image.width*9/12+'rpx'}" 
							mode='widthFix' 
							:src="item.img_url[0].url "
							v-if="item.img_url.length==1 && item.img_url[0].image.width<=900">
						</image>
						<view>
							<view v-if="item.img_url.length>=2 && item.img_url.length<=5 && item.img_url.length%2==0" class="image1">
							
								<u-image 
									class='u-image' 
									v-for='itemimg in item.img_url' 
									@tap='look(itemimg.url)'
										
									style='margin-left: 10rpx;margin-bottom: 10rpx;' 
									:src='itemimg.url' 
									width='45vw'
									height='45vw'
									>
								</u-image>
								
							</view>
							<view v-if="item.img_url.length==3 || item.img_url.length==6" class="image1">
							
								<u-image 
									class='u-image' 
									v-for='itemimg in item.img_url' 
									@tap='look(itemimg.url)'
									style='margin-left: 10rpx;margin-bottom: 10rpx;' 
									:src='itemimg.url' 
									width='30vw'
									height='30vw'>
								</u-image>
								
							</view>
							
							
						</view>
						<!-- <u-image border-radius='20rpx' width="100%" height="300rpx" src=""https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ab7b047f-4f41-46b9-b4be-f60dc317037a/cef2f555-d5ad-46c7-a38c-165c51e7307f.jpg" " v-if="item.img_url.length">
						</u-image> -->
					</view>
				</view>
				<view class="bot">
					<view>
						<u-icon name="star" :label="item.fabulous" color='#909399' size='40'></u-icon>
					</view>
					<view>
						<u-icon name="chat" size='40' label="0" color='#909399'></u-icon>
					</view>
					<view>
						<u-icon name="thumb-up" size='40' :label="item.collection" color='#909399'></u-icon>
					</view>
				</view>
			</view>
			<view style="background-color: #f3f4f6;width: 100%;height: 5rpx;margin-top: -60rpx;">
			</view>
		</view>



		<u-back-top :scroll-top="scrollTop"></u-back-top>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				scrollTop: 0,
				color:['#F0C461','#AF58F2','#F270D0','#4FDC46'],
				keyword: '',
				list: [],
				list1: [{
						image: 'https://cdn.uviewui.com/uview/swiper/1.jpg',
						title: '昨夜星辰昨夜风，画楼西畔桂堂东'
					},
					{
						image: 'https://cdn.uviewui.com/uview/swiper/2.jpg',
						title: '身无彩凤双飞翼，心有灵犀一点通'
					},
					{
						image: 'https://cdn.uviewui.com/uview/swiper/3.jpg',
						title: '谁念西风独自凉，萧萧黄叶闭疏窗，沉思往事立残阳'
					}
				],
				listtab: [
					{
							name: '最新动态'
					},
					{
						name: '表白墙'
					}, {
						name: '二手闲置',

					},
					{
						name: '快递代取',

					},
					{
						name: '失物招领',

					}
				],
				current: 0,
				databbai: []






			}
		},
		async onLoad() {
			await this.get(0)

		},
		computed: {
			slotRightCurrent() {
				return this.slotRight ? 0 : 1;
			}
		},
		methods: {
			//图片预览
			look(url) {
				console.log('0909090')
				var urlList = []
				urlList.push(url) //push中的参数为 :src="item.img_url" 中的图片地址
				uni.previewImage({
					indicator: "number",
			        loop: true,
					urls: urlList,
					longPressActions:true
				})

			},
			async get(val){
				//获取notice滚动通知
				const db = uniCloud.database()
				const res = await db.collection('nocite').get()
				this.list = res.result.data[0].text
				// 获取表白墙动态
				if(val==0){
					var restext = await db.collection("love,uni-id-users").field(
						'time,statetext,state,img_url,fabulous,text,collection,user_id._id,user_id.mobile,user_id.nickname,user_id.avatar_file.url'
						).orderBy('time desc').limit(28).get()
				}else{
					var restext = await db.collection("love,uni-id-users").where(`state==${val}`).field(
						'time,statetext,state,img_url,fabulous,text,collection,user_id._id,user_id.mobile,user_id.nickname,user_id.avatar_file.url'
						).orderBy('time desc').limit(28).get()
				}
				const data = restext.result.data
				this.databbai = data
				console.log(data)
			},
			onPageScroll(e) {
				this.scrollTop = e.scrollTop;
			},
			change(index) {
				this.current = index;
				
				this.get(this.current)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.u-image {
		max-width: 80vw;
		border-radius: 15rpx;
	}
	
	.image1 {
		margin-top: 20rpx;
		flex-wrap: wrap;
		display: flex;
	}

	.bot {
		display: flex;
		margin-top: 25rpx;
		justify-content: space-around;
	}

	.textitem {
		padding: 20rpx;
		margin-bottom: 60rpx;
	}
	
	.text {
		margin-top: 20rpx;
	}

	.content {
		display: flex;

		// padding: 20rpx;
		.name {
			display: flex;
			margin-left: 20rpx;
			flex-direction: column;
			justify-content: space-around;
		}
	}

	.search {
		background-color: #1CBBB4;
		height: 80rpx;
		// width: 80%;
	}

	.nav {
		display: flex;
		flex-wrap: wrap;
		// text-align: center;
	}

	.u-demo {
		//height: 200vh;
		height: calc(100% - 44px);
		height: calc(100% - 44px - constant(safe-area-inset-top));
		height: calc(100% - 44px - env(safe-area-inset-top));
	}

	.navitem .top {
		background-color: #F0C461;
		border-radius: 50%;
		width: 70rpx;
		height: 70rpx;
		margin-top: 30rpx;
		padding: 15rpx;

	}

	.navitem {
		width: 25%;
		display: flex;
		flex-direction: column;
		align-items: center;

	}

	.navitem .title {
		font-size: 30rpx;
		margin-top: 10rpx;
	}

	.wrap {
		padding: 24rpx;
	}

	.navbar-right {
		margin-right: 24rpx;
		display: flex;
	}

	.search-wrap {
		margin: 0 20rpx;
		flex: 1;
	}

	.right-item {
		margin: 0 12rpx;
		position: relative;
		color: #ffffff;
		display: flex;
	}

	.message-box {}

	.slot-wrap {
		display: flex;
		align-items: center;
		flex: 1;
	}

	.map-wrap {
		display: flex;
		align-items: center;
		margin-top: -15rpx;
		padding: 4px 6px;
		background-color: rgba(240, 240, 240, 0.35);
		color: #fff;
		font-size: 22rpx;
		border-radius: 100rpx;
		margin-left: 40rpx;
	}

	.map-wrap-text {
		padding: 0 6rpx;
	}
</style>
